// Item
.wp-block-kadence-tabs {
    .kt-tabs-title-list {
        margin:0;
        padding: 0;
        display: flex;
        flex-wrap: wrap;
        list-style: none;
        li {
            margin: 0 4px -1px 0;
            cursor: pointer;
            list-style: none;
            a.kt-tab-title {
                padding: 8px 16px;
                display:flex;
                color: #444;
                align-items: center;
                border-style: solid;
                border-color: transparent;
                border-width: 1px 1px 0 1px;
                border-top-left-radius: 4px;
				border-top-right-radius: 4px;
				text-decoration: none;
				transition: all .2s ease-in-out;
				&:focus {
					text-decoration: none;
				}
				&:hover {
					text-decoration: none;
				}
            }
            &.kt-tab-title-active {
				z-index: 4;
				text-decoration: none;
				position: relative;
                .kt-tab-title {
                    background-color: #fff;
                    border-color: #dee2e6;
                }
            }
        }
    }
}
.kt-tabs-icon-side-top .kt-tab-title {
    flex-direction: column;
}
.kt-tabs-accordion-title.kt-tabs-icon-side-top .kt-tab-title {
    align-items: flex-start;
}
.kt-tabs-accordion-title {
    .kt-tab-title {
        padding: 8px 16px;
        display:flex;
        color: #444;
        align-items: center;
        border-style: solid;
        border-color: transparent;
        border-width: 1px 1px 0 1px;
        border-top-left-radius: 4px;
        border-top-right-radius: 4px;
        transition: all .2s ease-in-out;
		text-decoration: none;
    }
    &.kt-tab-title-active {
        z-index: 4;
        .kt-tab-title {
            background-color: #fff;
            border-color: #dee2e6;
        }
    }
}
