// V tabs
.kt-tabs-layout-vtabs:after, .kt-tabs-wrap:after {
    clear: both;
    display: table;
    content: '';
}
.kt-tabs-layout-vtabs {
	> .kt-tabs-title-list {
		float: left;
		width: 30%;
		flex-direction: column;
		li {
			margin: 0 -1px 4px 0;
			.kt-tab-title {
				border-width: 1px 0px 1px 1px;
				border-top-left-radius: 0;
				border-top-right-radius: 0;
            }
            &.kt-tabs-icon-side-top .kt-tab-title {
                align-items: flex-start;
            }
		}
	}
	> .kt-tabs-content-wrap {
		float: left;
		width: 70%;
    }
    &.kt-tab-alignment-left > .kt-tabs-title-list li .kt-tab-title {
		align-items: center;
		justify-content: flex-start;
	}
	&.kt-tab-alignment-center > .kt-tabs-title-list {
		justify-content: flex-start;
		li {
			text-align: center;
			.kt-tab-title {
                justify-content: center;
                align-items: center;
			}
			.kb-tab-titles-wrap {
				align-items: center;
			}
		}
	}
	&.kt-tab-alignment-right > .kt-tabs-title-list {
		justify-content: flex-start;
		li {
			text-align: right;
			.kt-tab-title {
                justify-content: flex-end;
				align-items: center;
			}
			.kb-tab-titles-wrap {
				align-items: flex-end;
			}
		}
	}
}
